import React from 'react'
import logo from './images/favicon.ico'
export default class header extends React.Component{
	constructor(props){
    super(props)
    this.state={
      show:false
    }
    this.btnClick = this.btnClick.bind(this)
  }
  btnClick(){
    this.setState({
      show:!this.state.show
    })
  }
  render(){
		return(
				<div>
<nav className="navbar navbar-expand-lg navbar-dark  ">
  
    <a className="navbar-brand" href="/"><img className="img-fluid logo mt-2 ml-3 " src={logo} alt="image"/></a>
    <span className="nav-item  mr-3 mt-2">
        <span className="mr-3 d-inline heading">Prelims Current Affair's</span>
        <span className="mr-3 d-inline heading">Motivation</span>
        <span className="mr-3 d-inline heading">Study Material</span>
        <a className="mr-3 d-inline heading" href="https://blog.upsccurrent.com">Blog</a>
        <span className=" d-inline heading">Contact Us</span>
      </span>
    <button className="btn-nav justify-content-center mt-2" onClick={this.btnClick}>
     <ul >
      <li className="span1  "></li>
      <li className="span2 "></li>
    </ul>
    </button>
    {this.state.show ? 
      <button className="btn-nav justify-content-center mt-2" onClick={this.btnClick}>
      <ul>
        <li className="span3"></li>
        <li className="span4"></li>
      </ul>
      </button>
    :
    <button className="btn-nav justify-content-center mt-2" onClick={this.btnClick}>
     <ul >
      <li className="span1  "></li>
      <li className="span2 "></li>
    </ul>
    </button>
  }
</nav>
        {this.state.show ?
        <div className="nav-dialog pt-5">
            
            <ul className="pt-3  text-center">
              <li className="mb-3 pt-3">Prelims Current Affair's</li>
              <hr/>
              <li className="mb-3 pt-3">Motivation</li>
              <hr/>
              <li className="mb-3 pt-3">Study Material</li>
              <hr/>
              <a className="mr-3 pt-3 " href="https://blog.upsccurrent.com">Blog</a>
              <hr/>
              <li className="mb-3 pt-3">Contact Us</li>
              <hr/>
            </ul>
        </div>
        :
        <span></span>}
				</div>
			)
	}
}