import React from 'react'
import Loader from './loader'
import CardExpanded from './CardExpanded.jsx'
import Scrolling from 'react-horizontal-scrolling-menu'
import Card from './categoryCards.js'
import Card2 from './card2.js'
import ShowCard from '../components/showCard.js'
import * as Scroll from 'react-scroll'
import scrollToComponent from 'react-scroll-to-component';
import { Route, Link, BrowserRouter as Router } from 'react-router-dom'
export default class HomePage extends React.Component{
	constructor(props){
		super(props)
		this.state={
			loading:true,
			showContent:false,
			show:false,
			constList:this.props.list,
			sectionList:[],
			status:'',
			category:"All Articles",
			open:false,
			gprops:[],
			lessContentList:[],
			btnData:'More',
			showList:false,
			showPlus:'+'
		}
		this.categoryList=[
			{text:"",style:'1',secondtext:'',sstyle:'2'},
			{text:'',style:'3',secondtext:'',sstyle:'4'},
			{text:"",style:'1',secondtext:'',sstyle:'2'},
		]

		this.toggleBtn=this.toggleBtn.bind(this)
		this.showLess= this.showLess.bind(this)
		this.clicked=this.clicked.bind(this)
		this.toggleShow=this.toggleShow.bind(this)
		this.callback = this.callback.bind(this)
		this.callback2 = this.callback2.bind(this)
		this.closeExpanded = this.closeExpanded.bind(this)
		
	}
	callback2(props){
		this.callback(true,props)
	}
	toggleBtn(){
		if(this.state.btnData==="More"){
		this.setState({
			showList:!this.state.showList,
			btnData:"Close"
		})
	}
		else{
			this.setState({
				showList:!this.state.showList,
				btnData:'More'
			})
		}
	}
	closeExpanded(value){
		if(value===true)
		this.setState({
			open:false
		})
	}
	callback(value,gprops){
		console.log(value)
		this.setState({
			open:value,
			popValue:gprops
		})
	}
	componentDidMount(){
		let date1=new Date()
			let arr=[]
			let newList={}
			let second={}
			if(this.state.constList.length===0){
				this.setState({status:'No Article Found'})
			}
			else{
			arr=this.state.constList.text.filter(data=> data.section === "Reports" )
			
			newList.text=arr
			second.text=arr.slice(0,3)
			console.log(newList)
			if(newList.text.length===0)this.setState({status:'No Article Found',sectionList:newList})
			else{
			this.setState({
				sectionList:newList,
				status:'got',
				constList:newList,
				lessContentList:second
				})
			console.log(second)
			}
			}
	}
	showLess(){

		console.log('sectionlist',this.state.sectionList)
		if(this.state.sectionList.text.length>0){
		let arr=this.state.sectionList.text.slice(0,2)
		let newList={}
		newList.text=arr
		this.setState({
			lessContentList:newList
		})
		console.log('less content',this.state.lessContentList)
	}}
	toggleShow(){
		
	if(this.state.showPlus==="+"){
			this.setState({
				showPlus:'-'
			})
		}else{
			this.setState({
				showPlus:'+'
			})
		}
		this.setState({
			show: !this.state.show
		})
	

		if(!this.state.show){
			this.props.open('Reports')	
	
		}else{
			this.props.open("Today's Current")
		}
		
		
	}
	clicked(value){
		this.setState({
			category:value
		})
		console.log(value)
		var arr=[]
		let newList={}
		if(value === "All Articles"){
			arr=this.state.constList.text
		}else{
		 arr=this.state.constList.text.filter(data=> data.category===value )
		}
		
		newList.text=arr
		console.log('newlist',newList)
		if(newList.text.length===0)this.setState({status:'No Article Found',sectionList:newList})
			else{
			this.setState({
				sectionList:newList,
				status:'got'
			})
			
			}
			this.showLess()
	}
	render(){
		return(
			<div className="back mt-5" >
				<div className=" ml-2 mr-2 section clear" onClick={this.toggleShow}>
					<span className="text-section d-inline pl-3" >Reports</span>
					<span className="showPlus d-inline mr-4" onClick={this.toggleShow}>{this.state.showPlus}</span>
				</div>
						

				{this.state.show ?  		
				<div className="back">
				
				<div className="ml-3 mr-3 mb-2 clear">
					<Scrolling
					data={this.categoryList.map((data,key)=>
						
						<Card style={data.style} text={data.text} key={data.text}  sstyle={data.sstyle} secondText={data.secondtext} click={this.clicked}/>				
						
					)}
					alignCenter={false}
					scrollBy={1}
					hideArrows={true}
					/>
				</div>
				<span className="ml-4 mt-3 mb-3 d-inline-flex">
					<span className="dot mt-2 mr-2"></span>
					<span className="text-category mb-3">{this.state.category}</span>
				</span>
				{this.state.open ? <CardExpanded value={this.state.popValue} close={this.closeExpanded}/> : <span></span>}
				{this.state.status==='got' ?
				<div>
					<ShowCard value={this.state.lessContentList} open={this.callback}/> 
					<Link to="/article/reports" target="_blank">
					<button className="btn btn-more mr-4 mb-5">See All</button>
					</Link>
					{this.state.showList?
						
						<div className=" more-dialog">
							<div className="row">
						{this.state.sectionList.text.map((e,i)=>
							<div className="col-md-6 col-sm-6 col-xs-6 col-lg-6 mb-3 mt-3">
							<Card2 
							showExpanded={this.callback2}
							key={i}
							title={e.title}
                        subtitle={e.subTitle}
                        cont={e.content}
                        views={e.views}
                        lang={e.lang}
                        author={e.author}
                        thumbnail={e.photo}
                        metatags={e.metaTag}
                        tags={e.tag}
                        _id={e._id}/>
                        </div>
							)}
						</div>
						</div> 
						
						: <span></span>}

				 </div>
				 :  <h2 className="mt-4 mb-5 text-center">{this.state.status}</h2>  }
				</div>
				: <span></span>
				}

				 
			</div>
		)
	}
}