import React from 'react'
import Category from '../components/category'
import Header from '../components/header'
import Current from '../components/current'
import superagent from 'superagent'
import CardExpanded from '../components/CardExpanded.jsx'
import Loader from '../components/secondLoader'
import ShowCard from '../components/showCard.js'
import Environment from '../components/environment.js'
import DatePicker from  'react-date-picker'
import Art from '../components/art.js'
import Report from '../components/reports.js'
import International from '../components/international.js'
import Defence from '../components/defence.js'
import National from '../components/nationalbodies.js'
import Footer from '../components/footer.js'
export default class HomePage extends React.Component{
	constructor(props){
		super(props)
		this.state={
			latestList:[],
			category:"All Articles",
			open:false,
			popValue:[],
			loading:true,
			status:'',
			sectionList:[],
			constList:[],
			sectionConstList:[],
			showSection:"Today's Current",
			showCalender:true,
			showContent:"Today's Current",
			sectionAvail:false,
			dateCalender: new Date(),
			todayList:[],
			toshow:0
		}
		this.callback=this.callback.bind(this)
		this.changeCategory = this.changeCategory.bind(this)
		this.closeExpanded= this.closeExpanded.bind(this)
		this.arrayBufferToBase64=this.arrayBufferToBase64.bind(this)
		this.changeSection = this.changeSection.bind(this)
		this.toggelSection = this.toggelSection.bind(this)
		this.handleCalender=this.handleCalender.bind(this)
		this.getWidth=this.getWidth.bind(this)
	}
	closeExpanded(value){
		if(value===true)
		this.setState({
			open:false
		})
	}
	callback(value,gprops){
		console.log('inside homepage',value)
		console.log('gprops',gprops)
		this.setState({
			open:true,
			popValue:gprops
		})
	}
	componentDidMount(){
		this.fetchData()
		if(sessionStorage.getItem("upsccurrentDate") === null)
		{
			sessionStorage.setItem("upsccurrentDate",new Date())
		}else {
			this.tempdate=sessionStorage.getItem("upsccurrentDate")
			this.setState({
				dateCalender:this.tempdate
			})
			
		}
	}
	fetchData(){
		superagent.get('/api/content')
		.then((res)=>{
			console.log(res)
			if(res.text==="error in fetching data"){
				this.setState({
					loading:false,
					status:'Error in Fetching Data'
				})
			}
			else{
				var data=JSON.parse(res.text)
				console.log(data)
				if(data.text.length == 0){
					this.setState({
						loading:false,
						status:'No Article Found',
						sectionAvail:true
					})
				} else {
					this.setState({
						allList:data,
						latestList:data
					})

					var base64Flag = 'data:image/jpeg;base64,';
					this.state.latestList.text.map((data)=>{
						this.img=data.photo
						if(typeof(this.img.data.data)==="object" ){
						var imageStr = this.arrayBufferToBase64(this.img.data.data);
				        this.finalImg=base64Flag+imageStr
				        data.photo=this.finalImg
				        this.setState({
				        	img:this.finalImg
				        })
				    }
					})
					this.setState({
						loading:false,
						constList:this.state.latestList
					})	
				this.handleCalender(this.state.dateCalender)
				}
			}
		})
	}
	changeSection(value){
			let arr=[]
			let interList={}
			console.log('today lsit',this.state.todayList)
			arr=this.state.todayList.text.filter(data=>data.section==="Today's Current")
			interList.text=arr
			console.log(interList)
			if(interList.text.length===0)this.setState({status:'No Article Found',sectionConstList:interList,sectionAvail:true})
			else{
			this.setState({
				sectionList:interList,
				sectionConstList:interList
			},()=>this.getWidth())
		}
	}
	getWidth(){
		console.log('sectionList',this.state.sectionList)
		if(this.state.sectionList.text.length === 0){
			this.setState({
				status:'No Article Found'
			})
		}else{
		let value=0
		console.log(this.state.lessContentList)
		if(window.innerWidth > 860) value=3
		else if (window.innerWidth > 460 && window.innerWidth < 860) value=2
		else value=1
		
		if(value===3){
			if(this.state.sectionList.text.length < 3) value=this.state.sectionList.text.length
		}
		if(value===2){
			if(this.state.sectionList.text.length<2) value=this.state.sectionList.text.length
		}
		console.log('value',value)
		console.log('inside environment',this.state.sectionList.text.length)
		this.setState({
			toshow:value,
			status:'got',
			sectionAvail:true
		})
	}
	}
	changeCategory(value){
		this.setState({
			category:value,
			loading:true
		})
		let newList={}
		console.log('sectionConstList',this.state.sectionConstList)
		if(value === "All Articles"){
			newList.text=this.state.sectionConstList.text
		}else{

		let oldList=this.state.sectionConstList.text.filter(data=> data.category===value)	
		newList.text=oldList
		}
		console.log('new',newList)
		
		this.setState({
		sectionList:newList,
		loading:false,
		
		},()=>this.getWidth())
		
	}
	arrayBufferToBase64(buffer) {
    var binary = '';
    var bytes = [].slice.call(new Uint8Array(buffer));
    bytes.forEach((b) => binary += String.fromCharCode(b));
    return window.btoa(binary);
	}
	toggelSection(value){
		console.log('inside homepage closeSection',value)
		if(value!=="Today's Current"){
			this.setState({
				showContent:value,
				showCalender:false
			})
		}
		if(value==="Today's Current"){
			this.setState({
				showContent:"Today's Current",
				showCalender:true
			})
		}
	}
	handleCalender(indate){

		sessionStorage.setItem("upsccurrentDate",indate)
		let arr=[]
		let date
		if(sessionStorage.getItem("upsccurrentDate") !== null){
			date=new Date(sessionStorage.getItem("upsccurrentDate"))
			this.setState({
			dateCalender:date
		})
		}
		console.log('handle calned',date.getDate())
		if(this.state.latestList.text.map.length === 0){
			console.log('latestList length in handleCalender',this.state.latestList)
			let list={}
			list.text=[]
			this.setState({
				status:'No Articles',
				todayList:list
			},()=>this.changeSection("Today's Current"))
		}
		else{
		this.state.latestList.text.map((dat)=>{
			let data=new Date(dat.createdAt)		
			if((date.getDate() === data.getDate())&&((data.getMonth()+1)===(date.getMonth()+1))&&(date.getFullYear()===data.getFullYear())){
				arr.push(dat)
			}
		})
		let newList={}
		newList.text=arr
		console.log('handleCalender newList',newList)
		if(newList.text.length===0){
			this.setState({
				status:'No Articles',
				todayList:newList
			},()=>this.changeSection("Today's Current"))
		}
		else{
		this.setState({
			todayList:newList
		},()=> this.changeSection("Today's Current"))
			
		}
	}
	}
	render(){
		return(
			<div className="back">
				<Header/>


				<div className="calender-div " onClick={()=>this.toggelSection("Today's Current")}>
				<span className="text-section d-inline mt-5 ml-3">Today's Current</span>
				{this.state.showCalender ?
				<span className=" d-inline date-picker ml-3 mr-3"><DatePicker value={this.state.dateCalender} onChange={this.handleCalender}/></span> 
				 :<span className="showPlus d-inline mr-4" onClick={()=>this.toggelSection("Today's Current")}>+</span>
				}
				</div>
				{this.state.showContent==="Today's Current" ?
				<div>
				<div>
				<Category category={this.changeCategory}/>
				<br/><br/>
				</div>
				<div className="show-content">
					<span className="ml-4 d-inline-flex">
					<span className="dot mt-2 mr-2"></span>
					<span className="text-category mb-5">{this.state.category}</span>
					</span>
					{this.state.loading ? <div className="mt-5"><Loader/></div> : <span></span>}
					{this.state.status==='got' ? <ShowCard value={this.state.sectionList} toshow={this.state.toshow} open={this.callback}/> : <h2 className="mt-4 mb-5 pb-3 text-center">{this.state.status}</h2>}
					
				</div>
				</div>
				: <span></span>
				
				}
				{this.state.open ? <CardExpanded value={this.state.popValue} close={this.closeExpanded}/> : <span></span>}	
				{this.state.sectionAvail ?
					<div>
				 	<Environment list={this.state.constList} open={this.toggelSection}/>
				 	<Art list={this.state.constList} open={this.toggelSection}/> 
				 	<Report list={this.state.constList} open={this.toggelSection}/>
				 	<International list={this.state.constList} open={this.toggelSection}/>
				 	<Defence list={this.state.constList} open={this.toggelSection}/>
				 	<National list={this.state.constList} open={this.toggelSection}/>
					
					<Footer/>
					</div>
					: <span></span>}
				
			</div>
			)
	}
}