import React from 'react'
import Scrolling from 'react-horizontal-scrolling-menu'
import Card from './categoryCards.js'
const Arrow = ({ className }) => {
  return (
    <div
      className={className}
    >&#10140;</div>
  )
}
const ArrowR = ({className }) => {
  return (
    <div
      className={className}
    >&#10140;</div>
  )
}
const ArrowLeft = Arrow({  className: 'arrow-prev' });
const ArrowRight = ArrowR({  className: 'arrow-next' });
export default class Category extends React.Component{
	constructor(props){
		super(props)
		this.list=[
		{text:"All Articles",style:'1',secondtext:'National',sstyle:'2'},
		{text:'Economy',style:'3',secondtext:'Environment',sstyle:'4'},
		{text:'Political',style:'1',secondtext:'Science&Tech',sstyle:'2'},
		{text:'Budget',style:'3',secondtext:'EconomicSurvey',sstyle:'4'},
		{text:'Environment',style:'1',secondtext:'International',sstyle:'2'},
		{text:'Art',style:'3',secondtext:'Schemes',sstyle:'1'},
		{text:'Reports',style:'4',secondtext:'Economic Survey',sstyle:'1'},
		{text:'India Year Book',style:'2',secondtext:'International Bodies',sstyle:'3'},
		{text:'Geography',style:'4',secondtext:'Download',sstyle:'2'}
		]
		this.clicked = this.clicked.bind(this)
	}
	clicked(value){
		this.props.category(value)
	}

	render(){
		return(
			<div className="category mr-3 ml-1 pt-2 mb-1">
				<Scrolling
				data={this.list.map((data,key)=>
					
					<Card style={data.style} text={data.text} key={data.text}  sstyle={data.sstyle} secondText={data.secondtext} click={this.clicked}/>				
					
				)}
				alignCenter={false}
				scrollBy={1}
				arrowLeft={ArrowLeft}
          		arrowRight={ArrowRight}
          		hideSingleArrow={true}
				/>
          
			</div>
			)
	}
}