import React from 'react'
import ImageUpload from 'react-images-upload'
import superagent from 'superagent'
import ReactValidator from 'simple-react-validator'
import FileBase64 from 'react-file-base64'
import { Editor } from 'react-draft-wysiwyg'
import { EditorState , convertToRaw} from 'draft-js'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
export default class Admin extends React.Component{
	constructor(props){
		super(props)
		this.validator=new ReactValidator()
		this.state={
			author:[],
			category:this.props.category[0],
			btnText:"Save Article",
			files:[],
			editorState: EditorState.createEmpty(),
			form:[
				'inputTitle',
				'inputTags',
				'inputmetaTags',
				'inputLang',
				'inputSubTitle',
				'inputAuthor'
			]
		} 
		this.validator = new ReactValidator();
		this.handleChangeArticle= this.handleChangeArticle.bind(this)
		this.inputTitle=React.createRef()
		this.inputSubTitle=React.createRef()
		this.inputmetaTags=React.createRef()
		this.inputTags=React.createRef()
		this.inputArticle=React.createRef()
		this.inputAuthor=React.createRef()
		this.inputLang= React.createRef() 
		this.ititle=''
		this.iSubTitle=''
		this.iTags=''
		this.imetaTags=''
		this.iArticle=''
		this.iAuthor=''
		this.iLang=''
		this.pictureUpload=''
		this.selectChange=this.selectChange.bind(this)
		this.formSubmit = this.formSubmit.bind(this)
		this.formCheck=this.formCheck.bind(this)
		this.article=''
		var section=this.props.section
		this.toCategory=this.props.category
	}
	formCheck(e){
		e.preventDefault()
		this.ititle=this.inputTitle.current.value
		this.iSubTitle=this.inputSubTitle.current.value
		this.iTags=this.inputTags.current.value
		this.iAuthor=this.inputAuthor.current.value
		this.imetaTags=this.inputmetaTags.current.value
		this.iLang=this.inputLang.current.value
		this.pictureUpload=this.state.article
		if (this.validator.allValid()) {
    		this.formSubmit()
		  } else {
		    this.validator.showMessages()
		    this.forceUpdate()
		  }
}
	
	

	componentDidMount(){
		console.log(this.inputTitle.current.value)
	}
	selectChange(e){
		let {name,value}=e.target
		
		 this.setState({
		 	category:value
		 })
	}

	formSubmit(){
		this.setState({
			btnText:'Saving..'
		})
		const formData = new FormData()
		formData.append('photo',this.state.article)
		formData.append('title',this.inputTitle.current.value)
		formData.append('subTitle',this.inputSubTitle.current.value)
		formData.append('metaTag',this.inputmetaTags.current.value)
		formData.append('tag',this.inputTags.current.value)		
		formData.append('content',this.article)
		formData.append('lang',this.inputLang.current.value)
		formData.append('author',this.inputAuthor.current.value)
		formData.append('category',this.state.category)
		formData.append('section',this.props.section)

		superagent.post('/api/content')
		.set({'authorization': localStorage.token}) 
		.send(formData)
    	.then((res)=>{
    		console.log('res',res)
			if(res.text==="Success"){
				this.setState({
					btnText:'Saved!!'
				})
			}
			setTimeout(()=>{
				this.setState({
					btnText:'Save Article'
				})
			},12000)
		})
	}
	handleChangeArticle(picture){
		if(picture[0]){
		this.setState({
			article:picture[0]
		})}

	}
	 

  onEditorStateChange: Function = (editorState) => {
    this.setState({
      editorState,
    });
    this.article=draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))

  };
	render(){
		const { editorState } = this.state;
		return(
			
				<div className="container">
					<h4 className="text-center ed-head mt-4">Add Article</h4>
					<form className="mt-2 admin-form mb-3" onSubmit={this.formCheck}>
					  <div className="form-row">
					    <div className="form-group col-md-6">
					      <label  >Title</label>
					      <input type="text" className="form-control" ref={this.inputTitle} placeholder="Title"/>
					     	 {this.validator.message('title', this.ititle, 'required')}
					    </div>
					    <div className="form-group col-md-6">
					      <label >SubTitle</label>
					      <input type="text" className="form-control" ref={this.inputSubTitle} placeholder="SubTitle"/>
					    	{this.validator.message('Sub Title', this.iSubTitle, 'required')}
					    </div>
					  </div>
					  <div className="form-row">
					  <div className="form-group col-md-6 col-lg-6 col-sm-6">
					    <label>Meta Tags</label>
					    <input type="text" className="form-control" ref={this.inputmetaTags} placeholder="Meta Tags"/>
					  	{this.validator.message('Meta Tags', this.imetaTags, 'required')}
					  </div>
					  <div className="form-group col-md-6 col-lg-6 col-sm-6">
					    <label>Tags</label>
					    <input type="text" className="form-control" ref={this.inputTags} placeholder="Tags"/>
					  	{this.validator.message('Tags', this.iTags, 'required')}
					  </div>
					  
					  </div>
					  <div className="form-group">
					    <label>Article</label>
					     <Editor
				          editorState={editorState}
				          wrapperClassName="demo-wrapper"
				          editorClassName="demo-editor"
				          onEditorStateChange={this.onEditorStateChange}
				        />
				        {this.validator.message('article', this.article, 'required')}
					   </div>
					  <div className="form-row">
					    <div className="form-group col-md-4 col-sm-4 col-lg-4">
					      <label>Author</label>
					      <input type="text" className="form-control" ref={this.inputAuthor} placeholder="Author Name"/>
					    	{this.validator.message('Author', this.iAuthor, 'required')}
					    </div>
					    <div className="form-group col-md-4 col-sm-4 col-lg-4">
					      <label>Language</label>
					      <input type="text" className="form-control" ref={this.inputLang} placeholder="Language"/>
					    	{this.validator.message('Language', this.iLang, 'required')}
					    </div>
					    <div className="form-group col-md-4 col-sm-4 col-lg-4">
					      <label>Category</label>
					      <select ref="inputCategory" className="form-control" onChange={this.selectChange}>
					      	{this.toCategory.map((data)=> <option value={data}>{data}</option>)}
					      </select>
					    </div>
					   </div>
					   <div className="form-row">
					   		<div className="form-group text-center m-auto">
					   		<label>Article Image Upload:</label>
							<ImageUpload
				                withIcon={true}
				                buttonText='Choose image'
				                singleImage={true}
				                withPreview={true}
				                onChange={this.handleChangeArticle}
				                imgExtension={['.jpg', '.gif', '.png', '.gif']}
				                maxFileSize={5242880}
			            	/>
			            	{this.validator.message('Image', this.pictureUpload, 'required')}
					   		</div>
					   </div>
					   <div className="text-center">
					   
					   <button className="btn btn-primary text-center">{this.state.btnText}</button>
					   </div>
					   </form>
					   
			</div>
			)
	}
}