import React from 'react'
import Header from './header.jsx'
import superagent from 'superagent'
import Loader from './secondLoader.js'
import Scrolling from 'react-horizontal-scrolling-menu'
import Card from './categoryCards.js'
import Card2 from './card3.js'
import CardExpanded from './CardExpanded.jsx'
import ReactPaginate from 'react-paginate'
import Footer from './footer.js'
export default class Article extends React.Component{
	constructor(props){
		super(props)
		this.categories={
			"todaysCurrent":[{text:"All Articles",style:'1',secondtext:'National',sstyle:'2'},
							{text:'Economy',style:'3',secondtext:'Envirnoment',sstyle:'4'},
							{text:'Political',style:'1',secondtext:'Science&Tech',sstyle:'2'},
							{text:'Budget',style:'3',secondtext:'EconomicSurvey',sstyle:'4'},
							{text:'Environment',style:'1',secondtext:'International',sstyle:'2'},
							{text:'Art',style:'3',secondtext:'Schemes',sstyle:'1'},
							{text:'Report"s',style:'4',secondtext:'Economic Survey',sstyle:'1'},
							{text:'India Year Book',style:'2',secondtext:'International Bodies',sstyle:'3'},
							{text:'Geography',style:'4',secondtext:'Download',sstyle:'2'}],
			"environment":[{text:"All Articles",style:'1',secondtext:'Biodiversity',sstyle:'2'},
							{text:'National Park',style:'3',secondtext:'Biosphere',sstyle:'4'},
							{text:"Wildlife Sactuary",style:'1',secondtext:'Bodies',sstyle:'2'},
							{text:'Funds',style:'3',secondtext:'Conventions',sstyle:'4'},
							{text:"Reports",style:'1',secondtext:'Acts',sstyle:'2'},
							{text:'Schemes',style:'3',secondtext:'Water Bodies',sstyle:'4'},
							{text:"Species",style:'1',secondtext:'Miscellaneous',sstyle:'2'},
							{text:'Places',style:'3',secondtext:'',sstyle:''}],
			"art":[{text:"All Articles",style:'1',secondtext:'Theater',sstyle:'2'},
					{text:'Dance',style:'3',secondtext:'Temples',sstyle:'4'},
					{text:"Caves",style:'1',secondtext:'Forts',sstyle:'2'},
					{text:'Literature',style:'3',secondtext:'Painting',sstyle:'4'},
					{text:"Music",style:'1',secondtext:'Acts',sstyle:'2'},
					{text:'Places',style:'3',secondtext:'Persons',sstyle:'4'},
					{text:"News",style:'1',secondtext:'Miscellaneous',sstyle:'2'},
					{text:'Religion',style:'3',secondtext:'',sstyle:''}],
			"report":[{text:"",style:'1',secondtext:'',sstyle:'2'},
			{text:'',style:'3',secondtext:'',sstyle:'4'},
			{text:"",style:'1',secondtext:'',sstyle:'2'}],
			"international":[{text:"",style:'1',secondtext:'',sstyle:'2'},
			{text:'',style:'3',secondtext:'',sstyle:'4'},
			{text:"",style:'1',secondtext:'',sstyle:'2'}],
			"national":[{text:"",style:'1',secondtext:'',sstyle:'2'},
			{text:'',style:'3',secondtext:'',sstyle:'4'},
			{text:"",style:'1',secondtext:'',sstyle:'2'}],
			"defence":[{text:"",style:'1',secondtext:'',sstyle:'2'},
			{text:'',style:'3',secondtext:'',sstyle:'4'},
			{text:"",style:'1',secondtext:'',sstyle:'2'}]
		}
		var arr=this.props.location.pathname.split('/')
		this.state={
			pathVal:arr[2],
			section:'',
			loading:true,
			allList:[],
			pageList:[],
			sectionList:[],
			constList:[],
			categoryList:[],
			pages:0,
			pageDataLoading:false,
			selectedPage:1,
			secondList:[],
			openExpanded:false,
			popValue:[],
			noarticle:false
		}
		this.arrayBufferToBase64= this.arrayBufferToBase64.bind(this)
		this.changeCategory=this.changeCategory.bind(this)
		this.decideCategory= this.decideCategory.bind(this)
		this.filterSection = this.filterSection.bind(this)
		this.calPages=this.calPages.bind(this)
		this.calPageData=this.calPageData.bind(this)
		this.handlePageClick=this.handlePageClick.bind(this)
		this.callback2=this.callback2.bind(this)
		this.closeExpanded=this.closeExpanded.bind(this)
	}
	closeExpanded(value){
		if(value===true){
			this.setState({
				openExpanded:false
			})
		}
	}
	callback2(value,p){
		console.log('p',p)
		console.log('data',value)
		this.setState({
			popValue:p,
			openExpanded:true
		})
	}
	componentDidMount(){
		superagent.get('/api/content')
			.then((res)=>{
			if(res.text==="error in fetching data"){
				this.setState({
					loading:false,
					status:'Error in Fetching Data'
				})
			}
			else{
				var data=JSON.parse(res.text)
				if(data.text.length == 0){
					this.setState({
						loading:false,
						status:'No Article Found',
						sectionAvail:true
					})
				} else {
					this.setState({
						allList:data,
					})

					var base64Flag = 'data:image/jpeg;base64,';
					this.state.allList.text.map((data)=>{
						this.img=data.photo
						if(typeof(this.img.data.data)==="object" ){
						var imageStr = this.arrayBufferToBase64(this.img.data.data);
				        this.finalImg=base64Flag+imageStr
				        data.photo=this.finalImg
				        this.setState({
				        	img:this.finalImg
				        })
				    }
					})
					this.setState({
						
						constList:this.state.allList
					})	
					this.decideCategory()
				}
			}
		})
	}
	decideCategory(){
		var pathname=this.state.pathVal
		if(pathname==="todaysCurrent"){
			this.setState({
				section:"Today's Current",
				categoryList:this.categories[pathname]
			})
		} else if (pathname==='environment'){
			this.setState({
				section:'Environment',
		 categoryList:this.categories[pathname]
		 	})
		}else if (pathname==='art'){
			this.setState({
				section:'Art & Culture',
			  categoryList:this.categories[pathname]
			})
		}
		else if (pathname==='report'){
			this.setState({
				section:'Reports',
			  categoryList:this.categories[pathname]
			})
		}
		else if (pathname==='international'){
			this.setState({
				section:'International Bodies',
			  categoryList:this.categories[pathname]
			})
		}
		else if (pathname==='defence'){
			this.setState({
				section:'Defence Excercise',
			  categoryList:this.categories[pathname]
			})
		}
		else if (pathname==='national'){
			this.setState({
				section:'National Bodies',
			  categoryList:this.categories[pathname]
			})
		} else {
			this.setState({
				section:'no'
			})
		}
		this.filterSection()
	}
	filterSection(){
		console.log(this.state.categoryList)
		let arr = this.state.allList.text.filter((data)=> data.section === this.state.section)
		let newList={}
		newList.text=arr
		console.log('Section List',newList)
		this.setState({
			sectionList:newList,
			constList:newList
		})
		console.log('filter',newList)
		this.calPages(newList)
	}
	calPages(obj){
		console.log('calPages',obj)
		if(obj.text.length % 16 !==0){
			let pages = obj.text.length / 16
			pages=Math.floor(pages) + 1
			this.setState({
				pages:pages
			})
			let value=1
			this.calPageData(value,obj)
		}else{
			let pages= obj.text.length /16
			this.setState({
				pages:pages
			})
			let value=1
			this.calPageData(value,obj)
		}
	}
	calPageData(val,obj){
		console.log('pagecount',this.state.pages)
		console.log('calPagesdtat',obj)
		var startIndex=0 
		var lastIndex=0
		if(val === 1){
			 startIndex=(val-1)*16
			 lastIndex=(val)*16
		}
		else{
			 startIndex=(val-1)*16
			 lastIndex=(val)*16	
		}
		console.log(startIndex)
		console.log(lastIndex)
		let arr=obj.text.slice(startIndex,lastIndex)
		let newList={}
		newList.text=arr
		this.setState({
			pageList:newList,
			pageDataLoading:true,
			loading:false
		})
		console.log('pagedata',this.state.pageList)
	}
	handlePageClick(value){
		console.log('page value',value)
		var page=value.selected+1
		
		this.calPageData(page,this.state.sectionList)
	}
	arrayBufferToBase64(buffer) {
    var binary = '';
    var bytes = [].slice.call(new Uint8Array(buffer));
    bytes.forEach((b) => binary += String.fromCharCode(b));
    return window.btoa(binary);
	}
	changeCategory(value){
		if(value === "All Articles"){
			
			this.setState({
				sectionList:this.state.constList,
				noarticle:false
			})
			console.log('changecategory',this.state.sectionList)
			this.calPages(this.state.constList)
		} else{
		let arr = this.state.constList.text.filter((data)=>data.category === value)
		let newList={}
		console.log('val',value)
		newList.text=arr
		console.log(newList.text.length)
		if(newList.text.length===0){
			this.setState({
				noarticle:true,
				sectionList:newList,
				secondList:newList
			})
			this.calPages(newList)
		}else{
		console.log('newlist',newList)
		this.setState({
			sectionList:newList,
			secondList:newList,
			noarticle:false
		})
		console.log('change acategory',this.state.sectionList)
		this.calPages(newList)	
    	}}
	}
	render(){
		return(
			<div className="back ">
				<Header/>
				
				{this.state.section === 'no' ? <h2 className="text-center mt-5">Wrong Page</h2> : 
					<div>
				{this.state.loading? <div className="loader-container"><Loader/></div> 
				: 
				<div className="row-container ">
					<div className="text-section ml-4">{this.state.section}</div>
					<div className="ml-2">
					<Scrolling
					data={this.state.categoryList.map((data,key)=>
						
						<Card style={data.style} text={data.text} key={data.text}  sstyle={data.sstyle} secondText={data.secondtext} click={this.changeCategory}/>				
						
					)}
					alignCenter={false}
					hideArrows={true}
					/>
					</div>
					{this.state.pageDataLoading ?
					<div className="row-real mt-2">
						{(this.state.pageList.text.length===0)&&(!this.state.noarticle) ?<div className="text-center"> <h4 className="mt-5 mb-5 ">No Article Found</h4> </div>: <span></span>}
						
					<div className="row">
						{this.state.pageList.text.map((e,i)=>
							
								<div className="col-sm-4 col-lg-4 col-md-4">
									<Card2 
									showExpanded={this.callback2}
									key={i}
									title={e.title}
		                        subtitle={e.subTitle}
		                        cont={e.content}
		                        views={e.views}
		                        lang={e.lang}
		                        author={e.author}
		                        thumbnail={e.photo}
		                        metatags={e.metaTag}
		                        tags={e.tag}
		                        _id={e._id}/>
										</div>
							
						)}
						</div>
					</div>
					: <Loader/> }
					{this.state.noarticle? <h4 className=" text-center no-article">No Article Found</h4>: <span></span>}
					<div className="mr-auto container">
					  <ReactPaginate
				          previousLabel={'<'}
				          nextLabel={'>'}
				          breakClassName={'break-me'}
				          pageCount={this.state.pages}
				          marginPagesDisplayed={2}
				          pageRangeDisplayed={5}
				          onPageChange={this.handlePageClick}
				          containerClassName={'pagination'}
				          subContainerClassName={'pages pagination'}
				          activeClassName={'active'}
				        />
				    </div>
				    {this.state.openExpanded ? <CardExpanded value={this.state.popValue} close={this.closeExpanded}/> : <span></span>} 
				
				</div>
				
				}
				
				</div>

			}
			</div>
			)
	}
}